@charset "UTF-8";
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
li{
    list-style: none;
}
a{
    text-decoration: none;
}
a:hover{
    text-decoration: underline;
}
img{
    vertical-align: top;
}
body{
    font-size: 12px;
    color: #606060;
}
input{
    outline: none;
}
%header-style{
    /*头部logo样式*/
    width: 100%;
        height: 88px;
        padding-top: 8px;
        .logo{
            display: block;
            width: 210px;
            height: 80px;
            padding-left: 10px;
            float: left;
        }
}
@mixin style($w:1000px,$h:auto,$left:0) {
    /*总体内容*/
    width: $w;
    height: $h;
    margin: $left $h;
}
%main-style{
    /*中间内容样式*/
    width: 100%;
    height: 500px;
    padding: 20px 0px 10px 0px;
    position: relative;
}
//包裹表单的div样式
@mixin content-style( $w:380px,$t:50px,$r:34px) {
    width: $w;
    top: $t;
    right: $r;
    position: absolute;
    z-index: 99;
}
//边框样式
// @mixin boder($w:1px,$st:solid,$co:#dcdddd) {
//     border:$w $st $co;
// }

.register{
    //注册界面
    @include style();
    .header{
        @extend %header-style;
        .header-line{
            width: 100%;
            float: left;
            margin-top: 5px;
            border: 1px #43a41b solid;
        }
        
        .title{
            float: left;
            margin: 20px;
            font-size: 20px;
            line-height: 36px;
            color: #333333;
        }
        .tip{
            float: right;
            margin: 20px;
            font-size: 16px;
            line-height: 36px;
            span{
                color: #999;
            }
        }
        .tip a{
            color: #333333;
        }
        .tip a:hover{
            text-decoration: underline;
        }
    }
    .main{
        @extend %main-style;
        .bg{
            height: 500px;
            background: url(https://passport.womai.com/upload/2017/10/20/20171020031420155.jpg) no-repeat;
        }
        .content{
            @include content-style(335px,35px ,10px );
            form{
                width: 280px;
                border: #fff 1px solid;
                margin: 0 auto;
                margin-top: 33px;
                height: 375px;
                background-color: #fff;
            }
            form div{
                position: relative;
                width: 280px;
                height: 54px;
            }
            form div span{
                visibility: hidden;
                position: absolute;
                display: inline-block;
                width: auto;
                background: #fee6e7;
                border: 1px #faa2a6 solid;
                height: 18px;
                color: #ff8080;
                top: -19px;
                left: 0px;
                z-index: 99;
                padding: 0px 20px 0px 16px;
            }
            form div i{
                visibility: hidden;
                background: url(../img/jiantou.png)no-repeat;
                display: inline-block;
                width: 10px;
                height: 32px;
                position: absolute;
                top: 0;
                left: 18px;
            }
            form div input{
                width: 280px;
                height: 35px;
                padding: 5px 13px;
                color: #aaaaaa;
                font-size: 14px;
                outline: none;
            }
            form .submit{
                width: 277px;
                height: 36px;
                border: none;
                color: #fff;
                text-align: center;
                line-height: 36px;
                cursor: pointer;
                background-color: #43a41b;
                font-size: 18px;
            }
        }
    }
}
.footer{
    width: 100%;
    background: #efefef;
    li:nth-child(1){
        padding-top: 20px;
        width: 1000px;
        // height: 200px;
        margin: 0 auto;
        line-height: 26px;
        font-size: 12px;
        text-align: center;
        .beian a{
            color: #606060;
        }
        img{
            margin-top:4px;
        }
        .beian a:hover{
            text-decoration: underline;
        }
    }
    .register-end{
        height: 28px;
        background: #43a41b;
    }
}
